<template>
  <v-app style="background:none">
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs12>
          <v-card class="pa-4">
            <v-card-title>
              <v-icon large left>payment</v-icon>
              <span class="title font-weight-light">Simulador De Crédito</span>
            </v-card-title>

            <v-select
              v-model="tipoDeIngresoSeleccionado"
              :items="tiposDeIngreso"
              label="Tipo de Ingreso"
              outline
              color="#13D8F2"
            ></v-select>

            <v-text-field
              v-model="ingresoMensual"
              label="Tu Ingreso Mensual"
              outline
              color="#13D8F2"
            ></v-text-field>
            <v-text-field
              v-model="valorDelInmueble"
              @input="modificarMontoDeCredito = false, montoDeCreditoPorModificacion = undefined"
              label="Valor Del Inmueble"
              outline
              color="#13D8F2"
            ></v-text-field>
            <div @mouseover="infoMontoDeCredito = true" @mouseleave="infoMontoDeCredito = false">
              <div v-if="!modificarMontoDeCredito">
                <v-text-field
                  v-model="montoDeCreditoMaximoFicitcio"
                  @focus="modificarMontoDeCredito = true"
                  label="Monto de Crédito"
                  outline
                  append-icon="help"
                  color="#13D8F2"
                  :class="{'background-focus':infoMontoDeCredito}"
                ></v-text-field>
              </div>
              <div v-else>
                <v-text-field
                  v-model="montoDeCreditoPorModificacion"
                  @blur="validarModificacionDeMontoCredito"
                  label="Monto de Crédito"
                  outline
                  append-icon="help"
                  color="#13D8F2"
                  :class="{'background-focus':infoMontoDeCredito}"
                ></v-text-field>
              </div>
              <p
                v-show="infoMontoDeCredito"
                style="color:#13D8F2"
              >Si necesitas un credito menor, teclealo aquí</p>
            </div>

            <v-tabs v-model="tipoDePagoSeleccionado" centered grow slider-color="#13D8F2">
              <v-tab ripple>Pagos Fijos</v-tab>
              <v-tab ripple>Pagos Crecientes</v-tab>
            </v-tabs>

            <br>
            <br>
            <br>
            <v-bottom-nav :active.sync="plazoSeleccionado" :value="true" absolute>
              <v-btn color="#13D8F2" flat value="2">
                <span>Años</span>
                <span class="number-years">20</span>
              </v-btn>
              <v-btn color="#13D8F2" flat value="1">
                <span>Años</span>
                <span class="number-years">15</span>
              </v-btn>

              <v-btn color="#13D8F2" flat value="0">
                <span>Años</span>
                <span class="number-years">10</span>
              </v-btn>
            </v-bottom-nav>
          </v-card>
        </v-flex>
        <v-flex xs12 class="bancos-box">
          <div :key="index" v-for="(banco, index) in bancosPro">
            <v-card class="pa-4 mb-2" v-if="banco.valores.disponibilidad">
              <v-card-text class="px-0 mb-2">
                <img style="height:30px" :src="imagenesDeBancos[banco.nombre]">

                <div style="text-align:left">
                  <div>Aforo: {{(banco.valores.porcentajeDeCredito * 100).toFixed(2)}} %</div>
                  <div>Credito: $ {{banco.valores.montoDeCredito.toFixed(2)}}</div>
                  <br>
                  <div>Mensualidad: $ {{banco.valores.mensualidad.toFixed(2)}}</div>
                  <div>Ingresos: $ {{banco.valores.ingresos.toFixed(2)}}</div>
                  <div>Tasa: {{(banco.valores.tasa * 100).toFixed(2)}} %</div>
                  <div
                    v-show="creditoPreautorizado"
                  >Crédito Preautorizado: $ {{creditoPreautorizado.toFixed(2)}}</div>
                  <!-- <hr> enganche: {{banco.valores.enganche}} -->
                </div>
              </v-card-text>
            </v-card>
          </div>
        </v-flex>
      </v-layout>
    </v-container>

    <div
      is="hsbc"
      :tipo-de-ingreso="tipoDeIngresoSeleccionado"
      :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
      :valor-del-inmueble="valorDelInmueble"
      :plazo="plazoSeleccionado"
      :tipo-de-pago="tipoDePagoSeleccionado"
      :producto-seleccionado="productoSeleccionado"
    ></div>
    <div
      is="santander"
      :tipo-de-ingreso="tipoDeIngresoSeleccionado"
      :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
      :valor-del-inmueble="valorDelInmueble"
      :plazo="plazoSeleccionado"
      :tipo-de-pago="tipoDePagoSeleccionado"
      :producto-seleccionado="productoSeleccionado"
    ></div>
    <div
      is="banorte"
      :tipo-de-ingreso="tipoDeIngresoSeleccionado"
      :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
      :valor-del-inmueble="valorDelInmueble"
      :plazo="plazoSeleccionado"
      :tipo-de-pago="tipoDePagoSeleccionado"
      :producto-seleccionado="productoSeleccionado"
    ></div>
    <div
      is="scotiabank"
      :tipo-de-ingreso="tipoDeIngresoSeleccionado"
      :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
      :valor-del-inmueble="valorDelInmueble"
      :plazo="plazoSeleccionado"
      :tipo-de-pago="tipoDePagoSeleccionado"
      :producto-seleccionado="productoSeleccionado"
    ></div>
    <div
      is="banamex"
      :tipo-de-ingreso="tipoDeIngresoSeleccionado"
      :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
      :valor-del-inmueble="valorDelInmueble"
      :plazo="plazoSeleccionado"
      :tipo-de-pago="tipoDePagoSeleccionado"
      :producto-seleccionado="productoSeleccionado"
    ></div>
    <div
      is="banregio"
      :tipo-de-ingreso="tipoDeIngresoSeleccionado"
      :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
      :valor-del-inmueble="valorDelInmueble"
      :plazo="plazoSeleccionado"
      :tipo-de-pago="tipoDePagoSeleccionado"
      :producto-seleccionado="productoSeleccionado"
    ></div>
  </v-app>
</template>

<script>
import { mapState } from "vuex";

import Banamex from "./components/bancos/Banamex.vue";
import Banorte from "./components/bancos/Banorte.vue";
import Banregio from "./components/bancos/Banregio.vue";
import Hsbc from "./components/bancos/Hsbc.vue";
import Santander from "./components/bancos/Santander.vue";
import Scotiabank from "./components/bancos/Scotiabank.vue";

export default {
  name: "App",
  components: {
    Banamex,
    Banorte,
    Banregio,
    Hsbc,
    Santander,
    Scotiabank
  },
  data() {
    return {
      imagenesDeBancos: {
        banamex: "https://www.banamex.com/assets/img/citibanamex-logo.png",
        banorte:
          "https://upload.wikimedia.org/wikipedia/commons/1/13/Banorte.png",
        banregio:
          "http://upload.wikimedia.org/wikipedia/commons/thumb/e/e7/Banregio.png/440px-Banregio.png",
        hsbc:
          "https://upload.wikimedia.org/wikipedia/commons/thumb/a/aa/HSBC_logo_%282018%29.svg/1280px-HSBC_logo_%282018%29.svg.png",
        santander:
          "https://s3-eu-west-1.amazonaws.com/rankia/images/valoraciones/0024/8948/entrevista-banco-santander.png",
        scotiabank:
          "https://www.desafiodeguerreros.com.mx/wp-content/uploads/2015/12/Scotiabank-Logo-PNG-03791-1.png"
      },

      tiposDeIngreso: [
        "Asalariado",
        "Profesionista Independiente",
        "Independiente",
        "Socio de Empresa",
        "Negocio Propio"
      ],

      productoSeleccionado: "Comprar mi Casa",
      tipoDeIngresoSeleccionado: "",
      valorDelInmueble: "",
      valorDelCredito: 0,

      plazoSeleccionado: "2",
      tipoDePagoSeleccionado: 0,

      montoDeCreditoPorModificacion: undefined,
      modificarMontoDeCredito: false,

      ingresoMensual: "",

      infoMontoDeCredito: false
    };
  },
  methods: {
    validarModificacionDeMontoCredito() {
      if (
        this.montoDeCreditoPorModificacion == undefined ||
        this.montoDeCreditoPorModificacion == "" ||
        this.montoDeCreditoPorModificacion == 0
      ) {
        this.modificarMontoDeCredito = false;
        this.montoDeCreditoPorModificacion = undefined;
      } else {
      }
    }
  },
  computed: {
    bancosPro() {
      return this.bancos.sort((a, b) => {
        return (
          parseFloat(a.valores.mensualidad) - parseFloat(b.valores.mensualidad)
        );
      });
    },
    creditoPreautorizado() {
      if (this.tipoDeIngresoSeleccionado == "Asalariado") {
        return parseFloat(this.ingresoMensual) * 50;
      } else {
        return parseFloat(this.ingresoMensual) * 36;
      }
    },

    montoDeCreditoMaximoFicitcio() {
      return this.valorDelInmueble * 0.9;
    },

    ...mapState(["bancos"])
  },
  mounted() {
    setInterval(() => {
      this.$forceUpdate();
    }, 10);
  }
};
</script>
<style>
.number-years {
  font-size: 30px;
}

.background-focus * {
  background: none !important;
}
.background-focus .v-input__slot,
.background-focus .v-icon {
  border-color: rgba(19, 216, 242, 0.486) !important;
  color: rgba(19, 216, 242, 0.486) !important;
}

.bancos-box{
  height:90vh;
  max-height: 90vh;
  overflow-y: scroll
}
</style>
